// Heavily inspired from Primeflex https://github.com/primefaces/primeflex
@use '../0-settings/' as settings;
@use '../1-tools/' as tools;

$columns: 12;
$gutter: 0.5rem;

.sympl-grid {
  display: flex;
  flex-wrap: wrap;
  margin-right: -1 * $gutter;
  margin-left: -1 * $gutter;
  margin-top: -1 * $gutter;
}

.sympl-grid > .sympl-col,
.sympl-grid > [class*='sympl-col'] {
  box-sizing: border-box;
}

.sympl-grid-nogutter {
  margin-right: 0;
  margin-left: 0;
  margin-top: 0;
}

.sympl-grid-nogutter > .sympl-col,
.sympl-grid-nogutter > [class*='sympl-col-'] {
  padding: 0;
}

.sympl-col {
  flex-grow: 1;
  flex-basis: 0;
  padding: $gutter;
}

.sympl-col-fixed {
  flex: 0 0 auto;
  padding: $gutter;
}

$grid-columns: (
  'sympl-col-1': 8.3333%,
  'sympl-col-2': 16.6667%,
  'sympl-col-3': 25%,
  'sympl-col-4': 33.3333%,
  'sympl-col-5': 41.6667%,
  'sympl-col-6': 50%,
  'sympl-col-7': 58.3333%,
  'sympl-col-8': 66.6667%,
  'sympl-col-9': 75%,
  'sympl-col-10': 83.3333%,
  'sympl-col-11': 91.6667%,
  'sympl-col-12': 100%
);

$grid-column-offsets: (
  'sympl-col-offset-0': 0,
  'sympl-col-offset-1': 8.3333%,
  'sympl-col-offset-2': 16.6667%,
  'sympl-col-offset-3': 25%,
  'sympl-col-offset-4': 33.3333%,
  'sympl-col-offset-5': 41.6667%,
  'sympl-col-offset-6': 50%,
  'sympl-col-offset-7': 58.3333%,
  'sympl-col-offset-8': 66.6667%,
  'sympl-col-offset-9': 75%,
  'sympl-col-offset-10': 83.3333%,
  'sympl-col-offset-11': 91.6667%,
  'sympl-col-offset-12': 100%,
);

@each $col, $width in $grid-columns {
  .#{$col} {
    flex: 0 0 auto;
    padding: $gutter;
    width: #{$width};
  }
}

@each $key, $val in settings.$breakpoints {
  @media screen and (min-width: #{$val}) {
    .#{$key + settings.$responsive-separator} {
      @each $col, $width in $grid-columns {
        &#{$col} {
          flex: 0 0 auto;
          padding: $gutter;
          width: #{$width};
        }
      }
    }
  }
}

// Important usage here to disallow potential overrides of the offsets
// This is the approach primeflex takes as well.
@each $key, $val in $grid-column-offsets {
  .#{$key} {
    margin-left: #{$val} !important;
  }
}

@each $key, $val in settings.$breakpoints {
  @media screen and (min-width: #{$val}) {
    .#{$key + settings.$responsive-separator} {
      @each $key, $val in $grid-column-offsets {
        &#{$key} {
          margin-left: #{$val} !important;
        }
      }
    }
  }
}
