////
//// @group 1-tools
////

@use 'sass:map';
@use 'sass:meta';
@use '../0-settings/' as settings;

/// Gets the specified font size
/// @param {string} $font-size-name - one of 'xs', 'sm', 'md', 'lg', 'xl'
@function get-font-size($font-size-name) {
  $all-vars: meta.module-variables('settings');
  $key: 'font-size-#{$font-size-name}';

  @if not map.has-key($all-vars, $key) {
    @error 'there is no font size named [font-size-#{$font-size-name}]';
  }

  @return map.get($all-vars, $key);
}

/// Adds a 'font-size' style for the specified font size name
/// @param {string} $font-size-name - one of 'xs', 'sm', 'md', 'lg', 'xl'
@mixin font-size($font-size-name) {
  font-size: get-font-size($font-size-name);
}

/// Applies font styles for header1 - does not set the color
/// @param {boolean} $bold [false] - Make the font weight bold
@mixin header1($bold: false) {
  @include settings.font-family();
  @include font-size('lg');
  line-height: 1.5rem; // 24px;

  @if ($bold) {
    font-weight: 700;
  }
  @else {
    font-weight: 400;
  }
}

/// Applies font styles for header2 - does not set the color
@mixin header2() {
  @include settings.font-family();
  @include font-size('lg');
  font-weight: 700;
  line-height: 100%;
}

/// Applies font styles for body1 - does not set the color
/// @param {boolean} $bold [false] - Make the font weight bold
@mixin body1($bold: false) {
  @include settings.font-family();
  @include font-size('sm');
  line-height: 1.125rem; // 18px;

  @if ($bold) {
    font-weight: 700;
  }
  @else {
    font-weight: 400;
  }
}

/// Applies font styles for body1 - does not set the color
/// @param {boolean} $bold [false] - Make the font weight bold
@mixin body2($bold: false) {
  @include settings.font-family();
  @include font-size('xs');
  line-height: 1rem; // 16px;

  @if ($bold) {
    font-weight: 700;
  }
  @else {
    font-weight: 400;
  }
}
