////
//// @group 5-components
////

@use '../0-settings/spacing.scss';
@use '../0-settings/typography.scss';
@use '../1-tools/' as tools;

/// Applies custom styling to anchor tags
.sympl-link,
%sympl-link {
  @include tools.theme-element() using ($theme-name) {
    border: 1px solid transparent;
    border-radius: 4px;
    cursor: pointer;
    color: tools.get-semantic-slot('link.text', $theme-name);
    text-decoration: none;

    &:hover {
      color: tools.get-semantic-slot('link.text--hover', $theme-name);
      text-decoration: underline;
    }

    &:focus {
      border-color: tools.get-semantic-slot('link.border--focus', $theme-name);
      border-style: dotted;

      &::after {
        outline: none;
      }
    }

    &:focus-visible {
      outline: none;
    }
  }
}

/// Applies a button-like styling to anchor tags
.sympl-action-link {
  @extend %sympl-link;
  padding: tools.get-spacing('xs') tools.get-spacing('sm');
}
