////
//// @group 3-elements
////

// Single HTML element selectors without classes
@use 'sass:map';
@use '../0-settings/font.scss';
@use '../0-settings/typography.scss';
@use '../1-tools/' as tools;

html {
  @include font.font-family;
}

/// Body has the standard font family and font size set
///
/// Also sets foreground and background colors for each of the `theme-*` classes
body {
  @include tools.body1($bold: false);

  @include tools.theme-container() using ($theme-name) {
    background-color: tools.get-semantic-slot('body.background', $theme-name);
    color: tools.get-semantic-slot('body.text', $theme-name);
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  @include tools.theme-element() using ($theme-name) {
    color: tools.get-semantic-slot('body.header.background', $theme-name);
  }
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: tools.get-spacing('xs') 0;
}

h1 {
  font-size: typography.$headings-size-xl;
  height: typography.$headings-size-xl;
}

h2 {
  font-size: typography.$headings-size-lg;
  height: typography.$headings-size-lg;
}

h3 {
  font-size: typography.$headings-size-md;
  height: typography.$headings-size-md;
}

h4 {
  font-size: typography.$headings-size-sm;
  height: typography.$headings-size-sm;
}

h5 {
  font-size: typography.$headings-size-xs;
  height: typography.$headings-size-xs;
}

h6 {
  font-size: typography.$headings-size-xxs;
  height: typography.$headings-size-xxs;
}
