@use '../1-tools/' as tools;

.body1,
%body1 {
  @include tools.body1(false);
}

.body1-bold,
%body1-bold {
  @include tools.body1(true);
}

.body2,
%body2 {
  @include tools.body2(false);
}

.body2-bold,
%body2-bold {
  @include tools.body2(true);
}

.header1,
%header1 {
  @include tools.header1();
}

.header2,
%header2 {
  @include tools.header2();
}
