////
//// @group 0-settings
////


// Base font size used to compute all sizes below
$_font-size-base: 1rem;

/// Extra large header size.  Used in H1 tags
$headings-size-xl: 2 * $_font-size-base;

/// Large header size.  Used in H2 tags
$headings-size-lg: 1.875 * $_font-size-base;

/// Medium header size.  Used in H3 tags
$headings-size-md: 1.75 * $_font-size-base;

/// Small header size.  Used in H4 tags
$headings-size-sm: 1.5 * $_font-size-base;

/// Extra small header size.  Used in H5 tags
$headings-size-xs: 1.25 * $_font-size-base;

/// Extra extra small header size.  Used in H6 tags
$headings-size-xxs: 1.125 * $_font-size-base;


/// Extra large font size for standard typography
$font-size-xl: 1.5 * $_font-size-base;

/// Large font size for standard typography
$font-size-lg: 1.125 * $_font-size-base;

/// Medium font size for standard typography
$font-size-md: $_font-size-base;

/// Small font size for standard typography
$font-size-sm: 0.875 * $_font-size-base;

/// Extra small font size for standard typography
$font-size-xs: 0.75 * $_font-size-base;


/// font size for input fields?
$input-font-size: 0.875 * $_font-size-base;
