////
//// @group 5-components
////

@use '../0-settings/spacing.scss';
@use '../0-settings/typography.scss';
@use '../1-tools/' as tools;

/// Applies custom styling to table elements
///
/// Table rows can have `selectable` and `selected` classes applied and will be styled per Alloy guidelines.
.sympl-table {
  @include tools.theme-element() using ($theme-name) {
    border-collapse: collapse;
    box-sizing: border-box;
    text-align: left;
    width: 100%;

    %table-emphasis {
      font-size: typography.$font-size-xs;
      line-height: 150%;
      font-weight: 700;
      padding: tools.get-spacing('xs');
    }

    caption {
      font-size: typography.$font-size-lg;
      font-weight: 600;
      padding: tools.get-spacing('xs') 0;
      text-align: inherit;
    }

    th {
      @extend %table-emphasis;
    }

    td {
      font-size: typography.$font-size-sm;
      padding: tools.get-spacing('xs');
      box-sizing: border-box;
    }

    tbody td {
      position: relative;
      box-shadow: inset 0 -1px 0 0 tools.get-semantic-slot('table.row.border', $theme-name);
    }

    tbody tr {
      background-color: tools.get-semantic-slot('table.row.background', $theme-name);
      box-sizing: border-box;
      color: tools.get-semantic-slot('table.row.text', $theme-name);

      /* stylelint-disable selector-no-qualifying-type */
      &.selected {
        background-color: tools.get-semantic-slot('table.row.background--active', $theme-name);
      }

      &.selectable:hover {
        border-color: transparent;
        cursor: pointer;

        /* stylelint-disable-next-line selector-max-compound-selectors */
        td {
          box-shadow: inset 0 1px 0 0 tools.get-semantic-slot('table.row.border--hover', $theme-name), inset 0 -1px 0 0 tools.get-semantic-slot('table.row.border--hover', $theme-name);
        }
      }
      /* stylelint-enable selector-no-qualifying-type */
    }

    thead th {
      background-color: tools.get-semantic-slot('table.header.background', $theme-name);
      border: 2px solid tools.get-semantic-slot('table.header.border', $theme-name);
      border-bottom: 0;
      border-top: 0;
      box-sizing: border-box;
      color: tools.get-semantic-slot('table.header.text', $theme-name);
      vertical-align: middle;

      &:first-child {
        border-left: 0;
      }
    }

    tfoot td {
      @extend %table-emphasis;
      background-color: tools.get-semantic-slot('table.footer.background', $theme-name);
      box-sizing: border-box;
      color: tools.get-semantic-slot('table.footer.text', $theme-name);
    }
  }
}
