////
//// @group 1-tools
////

@use 'sass:color';
@use 'sass:map';
@use 'sass:math';
@use 'sass:meta';
@use 'sass:string';
@use '../0-settings/colors';
@use './sass-utilities';

/// Sets the theme map to use by all functions/mixins in this file
/// @access private
$_themes-to-use: colors.$alloy-themes;

/// Adjusts a color by computing the RGB value that results by adjusting it's opacity against a given background color.  The resulting color is opaque.
/// @param {color} $base-color - The original color to change
/// @param {percent|number} $opacity  - The ammount of opacity to apply.  (100% will result in no change, 0% will result in the background color)
/// @param {color} $background-color - The background color to use
/// @return {color} - The resulting color.  Note that this color has no alpha channel, i.e. it is opaque.
@function _adjust-color-with-opacity-and-background($base-color, $opacity, $background-color) {
  @if (meta.type-of($base-color) != 'color') {
    @error '$base-color #{$base-color} must be a color.';
  }
  @if (meta.type-of($background-color) != 'color') {
    @error '$background-color #{$background-color} must be a color.';
  }
  @if (meta.type-of($opacity) != 'number') {
    @error '$opacity #{$opacity} must be a raw number or percentage value.';
  }

  @if math.unit($opacity) != '%' {
    @if math.unit($opacity) != '' {
      @error '$opacity must be a raw number or percentage value.';
    }
    $opacity: math.percentage($opacity);
  }

  $rgba: rgba(color.red($base-color), color.green($base-color), color.blue($base-color), $opacity);
  @return color.mix(
    rgb(color.red($rgba), color.green($rgba), color.blue($rgba)),
    $background-color,
    color.alpha($rgba) * 100%
  );
}


/// Lightens a color by computing the RGB value that results by adjusting it's opacity against a pure white background.  The resulting color is opaque.
/// @param {color} $base-color - The original color to change
/// @param {percent|number} $opacity  - The ammount of opacity to apply.  (100% will result in no change, 0% will result in white)
/// @return {color} - The resulting color.  Note that this color has no alpha channel, i.e. it is opaque.
@function _lighten-color($base-color, $opacity) {
  @return _adjust-color-with-opacity-and-background($base-color, $opacity, #fff);
}


/// Darkens a color by computing the RGB value that results by adjusting it's opacity against a pure black background.  The resulting color is opaque.
/// @param {color} $base-color - The original color to change
/// @param {percent|number} $opacity  - The ammount of opacity to apply.  (100% will result in no change, 0% will result in black)
/// @return {color} - The resulting color.  Note that this color has no alpha channel, i.e. it is opaque.
@function _darken-color($base-color, $opacity) {
  @return _adjust-color-with-opacity-and-background($base-color, $opacity, #000);
}


/// Determines a pallete color by naming convention.  Convention is `name-{tint|shade}-{opacity}`  Ex 'mintyGreen-tint-75'
///
/// It is also valid to pass in only the base color name, ex: 'mintyGreen'
/// @param {string} $slot-name
/// @param {string} $theme-name ['primary']
/// @return {color} - The palette color
/// @access private
@function get-palette-color($slot-name, $theme-name: 'primary') {
  @if not map.has-key($_themes-to-use, $theme-name) {
    @error 'There is no theme named #{$theme-name}.  Valid themes are: #{map.keys($_themes-to-use)}';
  }
  $theme: map.get($_themes-to-use, $theme-name);
  $palette-map: sass-utilities.map-dotted-get($_themes-to-use, '#{$theme-name}.palette');

  $first-dash: string.index($slot-name, '-');

  // Handle the scenario where only the base color name was given
  @if ($first-dash == null) {
    $quick-color: map.get($palette-map, $slot-name);
    @if ($quick-color == null) {
      @error 'There is no base color named #{$slot-name}.  Valid colors are: #{map.keys($palette-map)}';
    }
    @return $quick-color;
  }

  $base-slot-name: string.slice($slot-name, 1, $first-dash - 1);
  $remainder: string.slice($slot-name, $first-dash + 1);
  $second-dash: string.index($remainder, '-');
  $tint-or-shade: string.slice($remainder, 1, $second-dash - 1);
  $opacity: string.slice($remainder, $second-dash + 1);
  $opacity-num: sass-utilities.to-number($opacity);
  @if ($opacity-num == null) {
    @error 'third part of palette slot name  (#{$slot-name}) must be a valid number between 0 and 100';
  }

  @if not map.has-key($palette-map, $base-slot-name) {
    @error 'There is no base color named #{$base-color-name}.  Valid colors are: #{map.keys($palette-map)}';
  }
  @if not ($tint-or-shade == 'tint' or $tint-or-shade == 'shade') {
    @error 'second part of palette slot name  (#{$slot-name}) must be "tint" or "shade"';
  }
  @if ($opacity-num < 0) or ($opacity-num > 100) {
    @error 'third part of palette slot name (#{$slot-name}) must be a number between 0 and 100';
  }

  $base-color: map.get($palette-map, $base-slot-name);

  @if ($tint-or-shade == 'tint') {
    @return _lighten-color($base-color, $opacity-num * 0.01);
  } @else {
    @return _darken-color($base-color, $opacity-num * 0.01);
  }
}

/// Returns the color for the given semantic slot and theme
/// @param {*} $slot-name - The semantic slot to get
/// @param {*} $theme-name ['primary'] - The theme to use, defaults to 'primary'
/// @return {color} - The color
@function get-semantic-slot($slot-name, $theme-name: 'primary') {
  @if not map.has-key($_themes-to-use, $theme-name) {
    @error 'There is no theme named #{$theme-name}.  Valid themes are: #{map.keys($_themes-to-use)}';
  }
  $semantic-slots: sass-utilities.map-dotted-get($_themes-to-use, '#{$theme-name}.semantic');

  $palette-color-name: sass-utilities.map-dotted-get($semantic-slots, $slot-name);
  @if $palette-color-name == null {
    @error 'There is no value for semantic slot #{$slot-name}';
  }

  @return get-palette-color($palette-color-name, $theme-name);
}


/// Generates container classes for every theme.
/// This mixin is expected to be called from within a container declaration: ex: body
/// @parameter {string} $default-theme [primary] - The theme name to use as the default
/// @content Content block is passed a single variable of the theme-name
/// @example scss
///   body {
///     @include theme-container() using ($theme-name) {
///       ... styles using $theme-name variable ...
///     }
///   }
///   -> body { ... }
///   -> body.theme-x { ... }
///   -> body.theme-y { ... }
@mixin theme-container($default-theme: 'primary') {
  @if not meta.content-exists() {
    @error 'theme-container mixin must be called with a content block that expects a single parameter';
  }

  @each $theme-name in map.keys($_themes-to-use) {
    @if $theme-name == $default-theme {
      & { /* stylelint-disable-line */
        @content($theme-name);
      }
    }

    &.theme-#{$theme-name} {
      @content($theme-name);
    }
  }
}

/// Generates selectors contained within every theme.
/// This mixin is expected to be called from within an element declaration: ex: .button or input
/// @parameter {string} $default-theme [primary] - The theme name to use as the default
/// @content [Content block is passed a single variable of the theme-name]
/// @example scss
///   .button {
///     @include theme-element() using ($theme-name) {
///       ... styles using $theme-name variable ...
///     }
///   }
///   -> .button { ... }
///   -> .theme-x .button { ... }
///   -> .theme-y .button { ... }
@mixin theme-element($default-theme: 'primary') {
  @if not meta.content-exists() {
    @error 'theme-element mixin must be called with a content block that expects a single parameter';
  }

  @each $theme-name in map.keys($_themes-to-use) {
    @if $theme-name == $default-theme {
      & { /* stylelint-disable-line */
        @content($theme-name);
      }
    }

    .theme-#{$theme-name} & {
      @content($theme-name);
    }
  }
}

/// Generates a :host-context() selector for every theme
/// Useful for breaking out of style-encapsulation scenarios like shadow-dom and angular components
/// @parameter {string} $default-theme [primary] - The theme name to use as the default
/// @content [Content block is passed a single variable of the theme-name]
/// @example scss
///   @include host-context() using ($theme-name) {
///     ... styles using $theme-name variable ...
///   }
///   -> host-context(*) { ... } // this is used for the 'default' theme
///   -> host-context(.theme-x) { ... }
///   -> host-context(.theme-y) { ... }
@mixin host-context($default-theme: 'primary') {
  @if not meta.content-exists() {
    @error 'host-context mixin must be called with a content block that expects a single parameter';
  }

  @each $theme-name in map.keys($_themes-to-use) {
    @if $theme-name == $default-theme {
      :host-context(*) {
        @content($theme-name);
      }
    }

    :host-context(.theme-#{$theme-name}) {
      @content($theme-name);
    }
  }
}
